.simple {
  position: relative;
  background-image: linear-gradient(to bottom, #F4E2C9 20%, #F4D7C9 100%);
}
.simple-drag {
  margin: 40px auto;
  padding: 10px;
  max-width: 350px;
  width: 90%;
}
.simple-drag-row {
  background: #fff;
  border-radius: 6px;
  margin: 5px auto;
  padding: 10px;
  transition: box-shadow 0.5s, transform 0.5s;
}
.simple-drag-row-title {
  font-weight: bold;
  font-size: large;
}



